	import HighCharts from '../components/HighCharts.vue'
	import _ from 'underscore'
	import axios from 'axios'

	export default {
		name: 'contact',

		components: {
			HighCharts
		},

		data() {
			return {
				id: 'contact-chart',
				chartData: [],
				rawData: [],
				options: {
					chart: {
						type: 'candlestick',
						renderTo: this.id
					},
					title: {
						text: 'BTC/USD History'
					},
					subtitle: {
						text: 'Source: cryptocompare.com'
					},
					rangeSelector: {
						selected: 1
					},
					series:  []

				}

			}
		},

		mounted () {
			this.requestData()
		},

		methods: {
			requestData () {
				axios.get('https://min-api.cryptocompare.com/data/histoday?fsym=BTC&tsym=USD&limit=30&aggregate=3&e=CCCAGG').
					then((response) => {
						console.log(this.options)
						this.options.series = response.data.Data
////						this.options.series.x = response.data.Data
////						this.options.series.data.x = this.rawData.map(entry => entry.time)
//
//						console.log(this.rawData)
					}).
					catch(err => {
						console.log(err)
					})
			}
		}
	}



