export default {
	props: {
		packageName: {
			type: String
		},
		totalDownloads: {
			type: Number
		},
		period: {
			type: String
		}
	},
	computed: {
		packageLink () {
			return `https://www.npmjs.com/package/${this.packageName}`
		}
	}
}



